import {BrowserModule} from '@angular/platform-browser'
import {NgModule} from '@angular/core'
import {HttpClientModule} from '@angular/common/http'

import {AppComponent} from './app.component'
import {HomeComponent} from './home/home.component'
import {ProgrammingComponent} from './programming/programming.component'
import {MusicComponent} from './music/music.component'
import {LiftingComponent} from './lifting/lifting.component'
import {PageNotFoundComponent } from './pagenotfound.component'
import {MyNumberPipe } from './pipes/mynumber.pipe'
import {MinutesSecondsPipe} from './pipes/minutesSeconds.pipe'

import {AppRoutingModule} from './app-routing.module'

import { PLATFORM_ID, APP_ID, Inject } from '@angular/core'
import { isPlatformBrowser } from '@angular/common'

@NgModule({
	imports: [
		BrowserModule.withServerTransition({ appId: 'egill-website' }),
		AppRoutingModule,
		HttpClientModule
	],
	declarations: [
		AppComponent,
		HomeComponent,
		ProgrammingComponent,
		MusicComponent,
		LiftingComponent,
		PageNotFoundComponent,
		MyNumberPipe,
		MinutesSecondsPipe
	],
	providers: [],
	bootstrap: [AppComponent]
})

export class AppModule {
	constructor(
		@Inject(PLATFORM_ID) private platformId: Object,
		@Inject(APP_ID) private appId: string) {
		const platform = isPlatformBrowser(platformId) ?
			'in the browser' : 'on the server'
		console.log(`Running ${platform} with appId=${appId}`)
	}
}
