import { Component, OnDestroy } from '@angular/core'
import { SoundCloudService } from './soundcloud.service'

interface ITrack {
	name: string
	id: number
	by?: string
	indexWithinBy?: number
	hasPlayedOnce?: boolean
	description?: string
	durationInSec?: number
	purchaseUrl?: string
	permalinkUrl?: string
	liked?: number
}

interface IByTracks {
	by: string
	tracks: ITrack[]
}

@Component({
	selector: 'app-music',
	templateUrl: './music.component.html',
	styleUrls: ['./music.component.css']
})
export class MusicComponent implements OnDestroy {
	isInitalized = false
	private SC
	private scPlayer
	selectedTrack: ITrack = {indexWithinBy: -1, name: 'select a track', id: -1, by: '', description: '', durationInSec: 0, hasPlayedOnce: false}
	selectedByIndex = -1
	openedUiByIndex = -1
	isTrackReady: boolean
	isPlaying: boolean
	timeInSec = 0

	private byGogogic = 'Audio for Gogogic'
	private byEgill = 'Egill Antonsson'
	private byKuai = 'KUAI'
	private byToo = 'Tribe of Oranges'
	private bySeniors = 'Jónas & Egill seniors'

	byTracksArr: IByTracks[]

	constructor(private soundCloudService: SoundCloudService) {
		this.SC = soundCloudService

		this.SC.initialize({
			client_id: 'NZtb1cCBbHFHV67f1Fp9jkGKog0H4StA'
		})

		const byGogogicTracks: ITrack[] = [
			{name: 'Vikings of Thule', id: 286944930},
			{name: 'Iceland Socks Intro', id: 307751458},
			{name: 'VoT (original mix)', id: 185133958}
		]
		const byEgillTracks: ITrack[]	= [
			{name: 'Fortíðin', id: 265756824},
			{name: 'Toddler Tune', id: 244801989},
			{name: 'Odd Times In Space', id: 273048355},
			{name: 'LeCube', id: 212448413}
		]
		const byKuaiTracks: ITrack[] = [
			{name: 'Pirringur', id: 493455450},
			{name: 'Apollo', id: 493455435},
			{name: 'Andsetinn', id: 493455411},
			{name: 'Hamskipti', id: 493455396},
			{name: 'Rover', id: 493455372},
			{name: 'Andefni', id: 493455357},
			{name: 'Agndofa', id: 493455342},
			{name: 'Ofurte', id: 493455318},
			{name: 'Lesblinda I', id: 493455303},
			{name: 'Lesblinda II', id: 493455276}
		]
		const byTooTracks: ITrack[] = [
			{name: 'Routine', id: 191481586},
			{name: 'Introduction', id: 214031252}
		]
		const bySeniorsTracks: ITrack[] = [
			{name: 'Rubber', id: 373876877},
			{name: 'Mouse', id: 374218076},
			{name: 'Gítarsólóin', id: 375296450},
			{name: 'Tromm', id: 377705372},
			{name: 'Pési', id: 375731609}
		]

		this.byTracksArr = [
			{by: this.byGogogic, tracks: byGogogicTracks},
			{by: this.byEgill, tracks: byEgillTracks},
			{by: this.byKuai, tracks: byKuaiTracks},
			{by: this.byToo, tracks: byTooTracks},
			{by: this.bySeniors, tracks: bySeniorsTracks}
		]

		for (let i = 0; i < this.byTracksArr.length; i++) {
			for (let j = 0; j < this.byTracksArr[i].tracks.length; j++) {
				const track = this.byTracksArr[i].tracks[j]
				track.indexWithinBy = j
				track.by = this.byTracksArr[i].by
				track.hasPlayedOnce = false
				track.description = ''
				track.durationInSec = 0
			}
		}
	}

	onByClick(byIndex: number) {
		if (this.openedUiByIndex === byIndex) {
			// deselect
			this.openedUiByIndex = -1
		} else {
			this.openedUiByIndex = byIndex
		}
	}

	onTrackClick(track: ITrack) {
		this.selectedByIndex = this.openedUiByIndex
		this.playNewStream(track)
	}

	onBuyClick() {
		window.open(this.selectedTrack.purchaseUrl)
	}

	playNewStream(selectedTrack: ITrack) {
		this.isInitalized = true
		this.selectedTrack = selectedTrack

		if (this.scPlayer != null) {
			this.scPlayer.kill()
		}
		this.isTrackReady = false
		const trackUrl = '/tracks/' + this.selectedTrack.id.toString()

		this.SC.stream(trackUrl, (player) => {

			console.log('player')
			console.log(player)

			this.scPlayer = player
			player.on('time', (millisec) => {
				const sec = Math.floor(millisec / 1000)
				if (this.timeInSec !== sec) {
					this.timeInSec = sec
				}
		})
		player.on('finish', () => {
			let trackIndex = 0
			if (this.selectedTrack.indexWithinBy === this.byTracksArr[this.selectedByIndex].tracks.length - 1) {
				if (this.selectedByIndex === this.byTracksArr.length - 1) {
					this.selectedByIndex = 0
				} else {
					this.selectedByIndex++
				}
			} else {
				trackIndex = this.selectedTrack.indexWithinBy + 1
			}
			this.openedUiByIndex = this.selectedByIndex
			this.playNewStream(this.byTracksArr[this.selectedByIndex].tracks[trackIndex])
		})
			player.play()
			this.isPlaying = true
		})

		this.SC.get(trackUrl, (track) => {

			console.log('track')
			console.log(track)


			this.isTrackReady = true
			if (!this.selectedTrack.hasPlayedOnce) {
				this.selectedTrack.hasPlayedOnce = true
				this.selectedTrack.description = track.description
				this.selectedTrack.durationInSec = Math.floor(track.duration / 1000)
				this.selectedTrack.purchaseUrl = track.purchase_url
				this.selectedTrack.permalinkUrl = track.permalink_url
				this.selectedTrack.liked = track.favoritings_count + 1
			}
		})
	}

	getDescriptionLines(): string[] {
		return this.selectedTrack.description.split('\n')
	}

	onPlayPause() {
		if (this.selectedTrack.id === -1) {
			this.selectedByIndex = this.openedUiByIndex = 0
			this.playNewStream(this.byTracksArr[this.selectedByIndex].tracks[0])
			return
		}
		if (this.scPlayer != null) {
			if (this.scPlayer.isPlaying()) {
				this.scPlayer.pause()
			} else {
				this.scPlayer.play()
			}
		}
		this.isPlaying = !this.isPlaying
	}

	ngOnDestroy() {
	if (this.scPlayer != null) {
			this.scPlayer.kill()
		}
	}
}
