import { Injectable } from '@angular/core'

@Injectable({
	providedIn: 'root',
})
export class SoundCloudService {
	private soundcloud

	constructor() {
		this.soundcloud  = require('soundcloud')
	}

	initialize(obj) {
		this.soundcloud.initialize(obj)
	}

	stream(trackUrl: string, callback) {
		this.soundcloud.stream(trackUrl).then((player) => {
			callback(player)
		})
	}

	get(trackUrl: string, callback) {
		this.soundcloud.get(trackUrl).then((track) => {
			callback(track)
		})
	}
}
